import React, { Component } from "react";
import { Container, Row, Col, Card, CardBody, CardTitle, CardText, Button } from "reactstrap";
import "./dashboard.scss";
import { Link } from "react-router-dom";

class Scholar extends Component {
    constructor(props) {
        super(props);
        this.state = {
            scholar_count: 0,
            data: [],
            allData: [
                { ronin: "ronin:442d11406ba023930268df815ad437d0afbfd5d1", percent: "33", name: "Corey" },
                { ronin: "ronin:ab8fcd71f8d6f741bd0e2fb21ccb53774d698899", percent: "69", name: "Adrianne" },
                { ronin: "ronin:9da19ce95adf96fefd353303b71e15cafff12140", percent: "12", name: "Marco" },
                { ronin: "ronin:f017bf38e15d329ec4ae24253096c3a98ff305ca", percent: "34", name: "Alex" },
                { ronin: "ronin:f0d8e43488d0ae5fdae1141ec626e1a1fdf2e79a", percent: "55", name: "John" },
                { ronin: "ronin:0aa51239ab97a3d7254d99bd736d4c5cfcb2dbfe", percent: "11", name: "Elsa" },
                { ronin: "ronin:ff3ae36cf419e8ba9ecae648d6851ea8fb62fdd3", percent: "55", name: "Anna" },
                { ronin: "ronin:ac26560d9788f7863b704493125d419246d59cb6", percent: "32", name: "Troll" },
                { ronin: "ronin:1538b88497e9e2af439e17e9c31d384ef20b5979", percent: "88", name: "Jane" },
                { ronin: "ronin:46258a9146922cd16e03127c928c4bf17eaa68e1", percent: "12", name: "Tom" }
            ],
            all_total:
            {
                "avg": 0,
                "tot_avg": 0,
                "today_slp": 0,
                "unclaimed": 0,
                "ronin_slp": 0,
                "scholar": 0,
                "manager": 0,
                "total": 0
            }
        }
    }
    sample() {
        fetch('https://api.metacache.app/data/0x442d11406ba023930268df815ad437d0afbfd5d1/battle')
            .then((response) => response.json())
            .then(booksList => {
                console.log(booksList);
            });
    }
    async componentDidMount() {
        let this_one = this;
        const db_data = this.state.allData;
        if (db_data.length !== 0) {
            document.getElementById("cover-spin").style.display = "inherit";
            for (let index = 0; index < db_data.length; index++) {
                let ronin = db_data[index].ronin.replace("ronin:", "0x");
                console.log(db_data[index].percent);

                // Get data of SLP
                var response = (await fetch('https://api.metacache.app/data/' + ronin + '/slp'));
                response = await response.json();
                console.log(response)

                // Get data of history
                var history = (await fetch('https://api.metacache.app/data/' + ronin + '/historical'));
                history =  await history.json();
                const daily_data = history.daily_data;
                console.log(daily_data);

                // Calculate scholar count
                this_one.setState({ scholar_count: index + 1 });

                // Calculate total slp
                const total_one = response.total_slp;
                const tot_total_one = this_one.state.all_total.total + total_one;

                // Calculate claimed slp
                const claimed = response.ronin_slp;
                const tot_claimed = this_one.state.all_total.ronin_slp + claimed;

                // Calculate unclaimed slp
                const unclaimed = total_one - claimed;
                const tot_unclaimed = this_one.state.all_total.unclaimed + unclaimed;

                // Calculate the last claimed date
                const timestamp = response.last_claim_timestamp;
                const oneDay = 24 * 60 * 60 * 1000; // hours*minutes*seconds*milliseconds
                const today = new Date();
                const last_date = new Date(timestamp * 1000);
                const last_claimed_date = last_date.toLocaleDateString();

                // // Calculate average slp 
                const diffDays = Math.ceil(Math.abs((today - last_date + 1) / oneDay));
                const avg_slp = Math.floor(unclaimed / diffDays);
                const tot_avg_slp = this_one.state.all_total.tot_avg + avg_slp;
                const real_avg_slp = Math.floor(tot_avg_slp / (index + 1));

                // Calculate manager
                const manager = Math.round(total_one / 100 * db_data[index].percent);
                const tot_manager = this_one.state.all_total.manager + manager;

                // Calculate scholar
                const scholar = total_one - manager;
                const tot_scholar = this_one.state.all_total.scholar + scholar;

                // Calculate today slp
                var today_slp, tot_today_slp;
                if (daily_data.length === 0) {
                    today_slp = 0;
                    tot_today_slp = this_one.state.all_total.today_slp
                }

                else {
                    today_slp = total_one - daily_data[daily_data.length-1].total_slp;
                    tot_today_slp = this_one.state.all_total.today_slp + today_slp;
                }
            
                // const today_so = 1;
                // const tot_today_so = this_one.state.all_total.today_so + today_so;

                // Calculate the next claim date
                const next_date_ready = new Date(last_date.setDate(last_date.getDate() + 14));
                const next_claim_date = next_date_ready.toLocaleDateString();

                // In-game slp 
                const in_game_slp = response.in_game_slp;
                // // Get the elo
                // var elo = response.data.mmr;

                // // Make array Data

                const new_data = {
                    "name": db_data[index].name,
                    "ronin": db_data[index].ronin,
                    "avg": avg_slp,
                    "last_claim": last_claimed_date,
                    "next_claim": next_claim_date,
                    "unclaimed": unclaimed,
                    "ronin_slp": claimed,
                    "scholar": scholar,
                    "today_slp": today_slp,
                    "manager": manager,
                    "total": total_one,
                    "in_game_slp": in_game_slp
                };

                this_one.setState(state => {
                    const data = state.data.concat(new_data);
                    return { data };
                });

                // Total object for all values
                const new_total = {
                    "avg": real_avg_slp,
                    "tot_avg": tot_avg_slp,
                    "unclaimed": tot_unclaimed,
                    "today_slp": tot_today_slp,
                    "ronin_slp": tot_claimed,
                    "scholar": tot_scholar,
                    "manager": tot_manager,
                    "total": tot_total_one
                }
                this_one.setState({ all_total: new_total });
                if (index === db_data.length - 1) {
                    document.getElementById("cover-spin").style.display = "none";
                    console.log(this.state.data, this.state.all_total)
                }
            }
        }
        else {
            document.getElementById("cover-spin").style.display = "none";
        }
    }
    render() {
        console.log(this.state.all_total.today_slp)
        const MainData = this.state.data.map((obj, index) => {
            return (
                <Card key={index}>
                        <Link   to= {"/user/"+obj.name+"/"+ this.state.allData[index].ronin+ "/" + this.state.allData[index].percent} className="to-user-link">
                            <Row className="ml-3 mr-3">
                                <Col xl={10}>
                                    <Row style={{ width: "100%", height: "100%" }}>
                                        <Col xl={4} className="pt-3">
                                            <Row>
                                                <Col xl={3}>
                                                    <div style={{ width: "50px", height: "50px", borderRadius: "50%", backgroundImage: "url(https://image.flaticon.com/icons/png/512/194/194938.png)", backgroundSize: "cover" }}>
                                                    </div>
                                                </Col>
                                                <Col>
                                                    <p className="main-font mb-0">{obj.name}</p>
                                                    <p className="mb-0" style={{ fontWeight: "bolder" }}>NEXT CLAIM: <span>{obj.next_claim}</span> </p>
                                                    <p className="mb-0" style={{ fontWeight: "bolder" }}>LAST CLAIM: <span>{obj.last_claim}</span> </p>
                                                </Col>
                                            </Row>
                                        </Col>
                                        <Col className="p-1 text-center pt-3">
                                        </Col>
                                    </Row>
                                </Col>
                                <Col xl={2}>
                                    <Row>
                                        <Col className="p-1 text-center pt-3">
                                            <span style={{ fontSize: "12px" }}>
                                                In-game SLP
                                            </span>
                                            <p className="main-font" style={{ fontSize: "18px" }}>{obj.in_game_slp}
                                                <span className="ml-1" style={{ position: "relative" }}>
                                                    <img src="https://s2.coinmarketcap.com/static/img/coins/64x64/5824.png" height="20" width="20" alt="SLP" />
                                                </span>
                                            </p>
                                        </Col>
                                        <Col className="p-1 text-center pt-3">
                                            <span style={{ fontSize: "12px" }}>
                                                Today SLP
                                            </span>
                                            <p className="main-font" style={{ fontSize: "18px" }}>{obj.today_slp}
                                                <span className="ml-1" style={{ position: "relative" }}>
                                                    <img src="https://s2.coinmarketcap.com/static/img/coins/64x64/5824.png" height="20" width="20" alt="SLP" />
                                                </span>
                                            </p>
                                        </Col>
                                    </Row>
                                </Col>
                            </Row>
                        </Link>
                    </Card>
            );
        });
        
        if (this.state.data.length !== 0 && this.state.data.length === this.state.allData.length)
        return (
            <React.Fragment>
                <div>
                    <Row style={{ "minHeight": "100vh", "margin": "0" }}>
                        <Col xl={2} className="main-blue">

                        </Col>
                        <Col xl={10}>
                            <Row>
                                <Col xl={9} className="pl-4 pr-4">
                                    <Row className="m-3" style={{ "borderBottom": "solid 1px rgb(229,231,235)" }}>
                                        <Col xl={9}>
                                            <p className="main-font m-0" style={{ "fontSize": "40px" }}>SCHOLARS</p>
                                            <h6>
                                                Your list of scholars
                                            </h6>
                                        </Col>
                                        <Col xl={3} className="pt-3" >
                                            <Button className="main-blue add-button">
                                                ADD NEW SCHOLAR
                                                <i className="fas fa-plus ml-3"></i>
                                            </Button>
                                        </Col>
                                    </Row>
                                    {MainData}
                                </Col>
                                <Col xl={3} className="p-4" style={{ "borderLeft": "solid 1px rgb(229,231,235)" }}>
                                    <Card className="text-center pt-4 pb-4">
                                        <CardBody>
                                            <CardTitle className="mt-0">TOTAL SCHOLARS</CardTitle>
                                            <div>
                                                <span className="main-font number-font">{this.state.allData.length}</span>
                                                <span className="ml-2" style={{ fontSize: "25px", top: "-4px", position: "relative" }}><i className="fas fa-user-graduate" /></span>
                                            </div>
                                        </CardBody>

                                    </Card>
                                    <Card className="text-center pt-4 pb-4">
                                        <CardBody>
                                            <CardTitle className="mt-0">TODAY EARNING</CardTitle>
                                            <CardText className="main-font" style={{ "fontSize": "30px", "fontWeight": "bolder" }}>
                                                <span className="main-font number-font">{this.state.all_total.today_slp}</span>
                                                <span className="ml-2" style={{ top: "-7px", position: "relative" }}>
                                                    <img src="https://s2.coinmarketcap.com/static/img/coins/64x64/5824.png" height="28" width="28" alt="SLP" />
                                                </span>
                                            </CardText>
                                        </CardBody>
                                    </Card>
                                    <Card className="text-center pt-4 pb-4">
                                        <CardBody>
                                            <CardTitle className="mt-0">TOTAL EARNINIGS</CardTitle>
                                            <CardText className="main-font" style={{ "fontSize": "30px", "fontWeight": "bolder" }}>
                                                <span className="main-font number-font">{this.state.all_total.total}</span>
                                                <span className="ml-2" style={{ top: "-7px", position: "relative" }}>
                                                    <img src="https://s2.coinmarketcap.com/static/img/coins/64x64/5824.png" height="28" width="28" alt="SLP" />
                                                </span>
                                            </CardText>
                                        </CardBody>
                                    </Card>
                                    <Card className="text-center pt-4 pb-4">
                                        <CardBody>
                                            <CardTitle className="mt-0">MANAGER EARNINGS</CardTitle>
                                            <CardText className="main-font" style={{ "fontSize": "30px", "fontWeight": "bolder" }}>
                                                <span className="main-font number-font">{this.state.all_total.manager}</span>
                                                <span className="ml-2" style={{ top: "-7px", position: "relative" }}>
                                                    <img src="https://s2.coinmarketcap.com/static/img/coins/64x64/5824.png" height="28" width="28" alt="SLP" />
                                                </span>
                                            </CardText>
                                        </CardBody>
                                    </Card>
                                </Col>
                            </Row>
                        </Col>
                    </Row>

                </div>
                <div id="cover-spin"></div>

            </React.Fragment>
        );
        else {
            return(
                <div id= "cover-spin"></div>

            )
        }
    }
}

export default Scholar;
