import React, { Component } from "react";
import { Container, Row, Col, Card, CardBody, CardTitle, CardText, Button } from "reactstrap";
import "./dashboard.scss";
import { Link } from "react-router-dom";
import UserChart from "../AllCharts/user-chart";
import * as moment from 'moment';
import axios from 'axios';


class User extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: [],
            date:[],
            daily_data: [],
            chart_y_data: []
        }
    }
    async componentDidMount() {
        let this_one = this;
        var ronin = this.props.match.params.ronin;
        document.getElementById("cover-spin").style.display = "inherit";
        ronin = ronin.replace("ronin:", "0x");
        // Get data of SLP
            var response = (await fetch('https://api.metacache.app/data/' + ronin + '/slp'));
            response = await response.json();
            console.log(response)

        // Get data of history
            var history = (await fetch('https://api.metacache.app/data/' + ronin + '/historical'));
            history =  await history.json();
            const daily_data = history.daily_data;
            this.setState({daily_data: daily_data});

        // Get data of battle
            var battle = (await fetch('https://api.metacache.app/data/' + ronin + '/battle'));
            battle = await battle.json();
            console.log(battle);
        // Get pesos price of the slp
            // var currency =  (await fetch('https://api.coingecko.com/api/v3/simple/token_price/ethereum?contract_addresses=0xcc8fa225d80b9c7d42f96e9570156c65d6caaa25&vs_currencies=php', {
            //     method: 'GET', // *GET, POST, PUT, DELETE, etc.
            //     mode: 'no-cors', // no-cors, *cors, same-origin
            //     cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
            //     credentials: 'same-origin', // include, *same-origin, omit
            //     headers: {
            //     'Content-Type': 'application/json'
            //     // 'Content-Type': 'application/x-www-form-urlencoded',
            //     },
            //     redirect: 'follow', // manual, *follow, error
            //     referrerPolicy: 'no-referrer', // no-referrer, *no-referrer-when-downgrade, origin, origin-when-cross-origin, same-origin, strict-origin, strict-origin-when-cross-origin, unsafe-url
            // }));
            // console.log(currency);
        // Calculate total slp
            const total_one = response.total_slp;

        // Calculate claimed slp
            const claimed = response.ronin_slp;

        // Calculate unclaimed slp
            const unclaimed = total_one - claimed;

        // Calculate the last claimed date
            const timestamp = response.last_claim_timestamp;
            const oneDay = 24 * 60 * 60 * 1000; // hours*minutes*seconds*milliseconds
            const today = new Date();
            const last_date = new Date(timestamp * 1000);
            const last_claimed_date = last_date.toLocaleDateString();

        // // Calculate average slp 
            const diffDays = Math.ceil(Math.abs((today - last_date + 1) / oneDay));
            const avg_slp = Math.floor(unclaimed / diffDays);

        // Calculate manager
            const manager = Math.round(total_one / 100 * this.props.match.params.percent);

        // Calculate scholar
            const scholar = total_one - manager;

        // Calculate today slp
        var today_slp;
        if (daily_data.length === 0) {
            today_slp = 0;
        }

        else {
            today_slp = total_one - daily_data[daily_data.length-1].total_slp;
        }
        
        // Calculate the next claim date
            const next_date_ready = new Date(last_date.setDate(last_date.getDate() + 14));
            const next_claim_date = next_date_ready.toLocaleDateString();

        // In-game slp 
            const in_game_slp = response.in_game_slp;

        // Make array Data

            const new_data = {
                "avg": avg_slp,
                "last_claim": last_claimed_date,
                "next_claim": next_claim_date,
                "unclaimed": unclaimed,
                "ronin_slp": claimed,
                "ign": battle.ign,
                "wins": battle.wins,
                "rank": battle.rank,
                "scholar": scholar,
                "today_slp": today_slp,
                "manager": manager,
                "total": total_one,
                "in_game_slp": in_game_slp
            };

            this_one.setState({data: new_data});
            document.getElementById("cover-spin").style.display = "none";
        
        var start_date;
        const { date } = this.state;
        if (daily_data.length !== 0) {
            start_date = new Date(daily_data[daily_data.length-2].date * 1000);
            start_date.setDate(start_date.getDate());
            if (daily_data.length > 7) {
                for (let index = 0; index < 7; index++) {
                    date.push(moment(start_date).format('YYYY-MM-DD'));
                    start_date.setDate(start_date.getDate() - 1);
                }
            }
            else{
                for ( var i = 0; i < daily_data.length-1; i++){
                    date.push(moment(start_date).format('YYYY-MM-DD'));
                    start_date.setDate(start_date.getDate() - 1);
                }
            }
        }
        this.setState( {date: date.reverse()} );
        var chart_y_data = [];
        if (daily_data.length !== 0) {
            if (daily_data.length > 7) {
                for (let index = 0; index < 7; index++) {
                    if (daily_data[daily_data.length-index -2].in_game_slp === 0) {
                        chart_y_data.push(0);
                    }
                    else{
                        var element =  daily_data[daily_data.length-index -2].total_slp - daily_data[daily_data.length -index-3].total_slp;
                        chart_y_data.push(element);
                    }
                    
                }
            }
            else{
                for (let index = 0; index < daily_data.length - 2; index++) {
                    if (daily_data[daily_data.length-index -2].in_game_slp === 0) {
                        chart_y_data.push(0);
                    }
                    else{
                         var element =  daily_data[daily_data.length-index -2].total_slp - daily_data[daily_data.length -index-3].total_slp;
                        chart_y_data.push(element);
                    }
                   
                }
            
            }
            
        }

        this.setState({chart_y_data: chart_y_data.reverse()})
    }
        
    render() {
        console.log(this.state.daily_data);
        var options;
        if (this.state.chart_y_data.length !==0) {
            options = {
                scales: {
                    yAxes: [{
                        ticks: {
                            max: Math.ceil(Math.max(...this.state.chart_y_data) / 30) * 30,
                            min: 0,
                            stepSize: 30
                        }
                    }]
                }
            }
        }
        else{
            options = 0
        }
        var data_for_chart = {
            labels:this.state.date,
            datasets: [
                {
                    label: "Daily SLP",
                    fill: true,
                    lineTension: 0.5,
                    backgroundColor: "rgba(85, 110, 230, 0.2)",
                    borderColor: "#556ee6",
                    borderCapStyle: 'butt',
                    borderDash: [],
                    borderDashOffset: 0.0,
                    borderJoinStyle: 'miter',
                    pointBorderColor: "#556ee6",
                    pointBackgroundColor: "#fff",
                    pointBorderWidth: 1,
                    pointHoverRadius: 5,
                    pointHoverBackgroundColor: "#556ee6",
                    pointHoverBorderColor: "#fff",
                    pointHoverBorderWidth: 2,
                    pointRadius: 1,
                    pointHitRadius: 10,
                    data: this.state.chart_y_data
                }
            ]
        }
        return (
            <React.Fragment>
                <div>
                    <Row style={{ "minHeight": "100vh", "margin": "0" }}>
                        <Col xl={12} className="pl-4 pr-4">
                            <Row className="m-3" style={{ "borderBottom": "solid 1px rgb(229,231,235)" }}>
                                <Col xl={11}>
                                    <p className="main-font m-0" style={{ "fontSize": "40px" }}>
                                        {this.props.match.params.username}
                                    </p>
                                    <h6>
                                        {this.props.match.params.ronin}
                                    </h6>
                                </Col>
                                <Col xl={1} className="pt-3" >
                                    <Button className="main-blue add-button">
                                        EDIT
                                        <i className="fas fa-edit ml-3"></i>
                                    </Button>
                                </Col>
                            </Row>
                            <Row className="m-0 pl-3">
                                <Col className="d-flex">
                                    <p className="grey-font-color summary-font mr-2">Summary</p>
                                    <span style={{fontSize:"1.3rem"}}><i className="fas fa-question-circle"></i></span>
                                    
                                </Col>
                                <Col>
                                    <p className="float-right mr-2">
                                        <span className="main-font">{this.state.data.scholar}</span>
                                        <span className="ml-2" style={{ top: "-2px", position: "relative" }}>
                                            <img src="https://s2.coinmarketcap.com/static/img/coins/64x64/5824.png" height="30" width="30" alt="SLP" />
                                        </span>
                                    </p>
                                </Col>
                            </Row>
                            <Col className="pl-4 pr-4 mt-3">
                                <Row>
                                    <Col xl={4}>
                                        <Card>
                                            <div>
                                                <span style={{fontSize:"1.3rem", float:"right"}} className="mt-1 mr-2">
                                                    <i className="fas fa-exclamation-circle"></i></span>
                                            </div>
                                            <p className="ml-4 grey-font-color">Expected Earnings as of today</p>
                                            <CardText className="ml-4 mb-0" style={{ "fontSize": "22px", "fontWeight": "bolder" }}>
                                                <span className="grey-font-color">{this.state.data.scholar}</span>
                                                <span className="ml-2" style={{ top: "-2px", position: "relative" }}>
                                                    <img src="https://s2.coinmarketcap.com/static/img/coins/64x64/5824.png" height="21" width="21" alt="SLP" />
                                                </span>
                                            </CardText>
                                            <p className="ml-4 grey-font-color">This is not yet final and still subject to change</p>
                                        </Card>
                                    </Col>
                                    <Col xl={8}>
                                        <Card>
                                            <Row>
                                                <Col xl={12}>
                                                    <span style={{fontSize:"1.3rem", float:"right"}} className="mt-1 mr-2">
                                                        <i className="fas fa-gamepad"></i></span>
                                                </Col>
                                                <Col xl={12} className="pl-5">
                                                    <p className="main-font h5">
                                                        {this.state.data.ign}
                                                    </p>
                                                </Col>
                                                <Col xl={3} className="pl-5">
                                                    
                                                    <div>
                                                        <p className="font-weight-bolder mb-0">
                                                            <span style={{color:"rgb(241,151,65)"}} className="mt-1 mr-2">
                                                                <i className="fas fa-trophy"></i>
                                                            </span>
                                                            {this.state.data.wins}
                                                        </p>
                                                        <p className="font-weight-bolder mb-0">
                                                            <span style={{color:"rgb(241,151,65)"}} className="mt-1 mr-2">
                                                                <i className="fas fa-star"></i>
                                                            </span>
                                                            {this.state.data.rank}
                                                        </p>
                                                        <div className="grey-font-color">
                                                            <span className="font-weight-bolder mr-1">Last Claim:</span>
                                                            {this.state.data.last_claim}
                                                        </div>
                                                        <div className="grey-font-color">
                                                            <span className="font-weight-bolder mr-1">Next Claim:</span>
                                                            {this.state.data.next_claim}
                                                        </div>
                                                        
                                                    </div>
                                                </Col>
                                                <Col xl={3} className="text-center">
                                                    <p className="grey-font-color h6">
                                                        SLP Earned Today
                                                    </p>
                                                    <CardText className="main-font" style={{ "fontSize": "22px", "fontWeight": "bolder" }}>
                                                        <span className="main-font">{this.state.data.today_slp}</span>
                                                        <span className="ml-2" style={{ top: "-2px", position: "relative" }}>
                                                            <img src="https://s2.coinmarketcap.com/static/img/coins/64x64/5824.png" height="21" width="21" alt="SLP" />
                                                        </span>
                                                    </CardText>
                                                </Col>
                                                <Col xl={3} className="text-center">
                                                    <p className="grey-font-color h6">
                                                        Total Unclaimed SLP
                                                    </p>
                                                    <CardText className="main-font" style={{ "fontSize": "22px", "fontWeight": "bolder" }}>
                                                        <span className="main-font">{this.state.data.unclaimed}</span>
                                                        <span className="ml-2" style={{ top: "-2px", position: "relative" }}>
                                                            <img src="https://s2.coinmarketcap.com/static/img/coins/64x64/5824.png" height="21" width="21" alt="SLP" />
                                                        </span>
                                                    </CardText>
                                                </Col>
                                                <Col xl={3} className="text-center">
                                                    <p className="grey-font-color h6">
                                                        Overall SLP Claimed
                                                    </p>
                                                    <CardText className="main-font" style={{ "fontSize": "22px", "fontWeight": "bolder" }}>
                                                        <span className="main-font">{this.state.data.total}</span>
                                                        <span className="ml-2" style={{ top: "-2px", position: "relative" }}>
                                                            <img src="https://s2.coinmarketcap.com/static/img/coins/64x64/5824.png" height="21" width="21" alt="SLP" />
                                                        </span>
                                                    </CardText>
                                                </Col>
                                            </Row>
                                            
                                        </Card>
                                    </Col>

                                </Row>
                                <Row>
                                    <Col xl={8} className="p-3 bg-white mb-4">
                                        {this.state.date.length ===0 ? <h1 className="main-font">No Data of Daily SLP</h1> 
                                        : <UserChart data={data_for_chart} options={options}/> }
                                       
                                    </Col>
                                    <Col xl={4} >
                                        <Card className="p-2">
                                            <div>
                                                <span style={{fontSize:"1.3rem", float:"right"}} className="mt-1 mr-2">
                                                    <i className="fas fa-list"></i></span>
                                            </div>
                                            <p className="grey-font-color h6 ml-4">
                                                Axie List
                                            </p>
                                            <Row className="border-bottom border-light ml-2 mr-2">
                                                <Col >
                                                    <div className="text-center">
                                                        <img src="https://storage.googleapis.com/assets.axieinfinity.com/axies/1059556/axie/axie-full-transparent.png" alt="bird" width="200" height="150"/>
                                                    </div>
                                                </Col>
                                                <Col className="pt-5">
                                                    <h6 className="grey-font-color mb-1">Bird</h6>
                                                    <h4 className="main-font">Axie #1858884</h4>
                                                </Col>
                                            </Row>
                                            <Row className="border-bottom border-light ml-2 mr-2">
                                                <Col>
                                                    <div className="text-center">
                                                        <img src="https://storage.googleapis.com/assets.axieinfinity.com/axies/951869/axie/axie-full-transparent.png" alt="bird" width="200" height="150"/>
                                                    </div>
                                                </Col>
                                                <Col className="pt-5">
                                                    <h6 className="grey-font-color mb-1">Plant</h6>
                                                    <h4 className="main-font">Axie #1859387</h4>
                                                </Col>
                                            </Row>
                                            <Row>
                                                <Col >
                                                    <div className="text-center">
                                                        <img src="https://lynnyl.io/wp-content/uploads/2021/07/Arco-1024x768.png" alt="beast" width="200" height="150"/>
                                                    </div>
                                                </Col>
                                                <Col className="pt-5">
                                                    <h6 className="grey-font-color mb-1">Beast</h6>
                                                    <h4 className="main-font">Axie #1859172</h4>
                                                </Col>
                                            </Row>
                                        </Card>
                                    </Col>
                                </Row>
                            </Col>
                        </Col>
                    </Row>

                </div>
                <div id="cover-spin"></div>

            </React.Fragment>
        );
        
    }
}

export default User;
